import React from "react";
import { Card, Title } from "@vkontakte/vkui";

const style = {
  span: {
    display: "flex",
    alignItems: "center",
    padding: ".5rem 1rem",
    border: "1px solid #ccc",
    borderRadius: "4px",
    marginBottom: ".5rem",
  },
};

function NotesPreview({ notesId, title, onClick }) {
  return (
    <Card
      size="l"
      mode="shadow"
      onClick={() => onClick((notesId = { notesId }))}
    >
      <span style={style.span}>
        <Title weight="bold">{title}</Title>
      </span>
    </Card>
  );
}

export default NotesPreview;
